/* General styles */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f4f4f9;
  }
  
  h1 {
    text-align: center;
    color: #333;
  }
  
  /* Container for the Treap visualizer */
  .treap-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 50px;
  }
  
  input {
    padding: 10px;
    margin: 5px;
    width: 200px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  button {
    padding: 10px 15px;
    margin: 10px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #45a049;
  }
  
  /* Style for the tree visualization */
  pre {
    font-size: 16px;
    background-color: #fff;
    padding: 15px;
    border-radius: 5px;
    border: 1px solid #ddd;
    margin-top: 20px;
    white-space: pre-wrap;
    word-wrap: break-word;
  }

  
  /* styles.css */

/* 1. Cho SVG co giãn theo chiều ngang */
svg {
  display: block;          /* để margin: auto hoạt động */
  max-width: 100%;         /* không vượt quá 100% kích thước cha */
  height: auto;            /* cao sẽ điều chỉnh tỉ lệ */
  margin: 0 auto;          /* căn giữa ngang */
}

/* 2. Nếu muốn thêm scroll khi zoom quá to */
.container {
  width: 100%;             /* hoặc một kích thước cố định, ví dụ 1000px */
  height: 600px;           /* giữ đúng chiều cao bạn muốn */
  overflow: auto;          /* bật thanh cuộn khi nội dung vượt khung */
}

/* 3. Tùy chỉnh vùng controls */
.controls {
  margin-bottom: 10px;
  display: flex;
  gap: 8px;
}

.controls input {
  width: 120px;
  padding: 4px;
}

.controls button {
  padding: 6px 12px;
  cursor: pointer;
}
